/* eslint-disable no-restricted-globals */
/* eslint-disable camelcase */
import { sentenceCase  } from 'change-case';
import { useState, useEffect, } from 'react';

// @mui
// @mui
import { useTheme } from '@mui/material/styles';
import {
  Card,
  Table,
  TableRow,
  TableBody,
  TableCell,
  Container,
  TableContainer,
  Switch,
  Backdrop,
  CircularProgress
} from '@mui/material';
// swal 
// format
import FormControlLabel from '@mui/material/FormControlLabel';
// routes
import { PATH_DASHBOARD } from '../../routes/paths';
import Label from '../../components/Label';
// hooks
import useSettings from '../../hooks/useSettings';
// components
import Page from '../../components/Page';
import Scrollbar from '../../components/Scrollbar';
import HeaderBreadcrumbs from '../../components/HeaderBreadcrumbs';
// format number 
import { fCurrency,} from '../../utils/formatNumber';
// sections
import { HabukhanListHead} from '../../sections/admin/user/list';


// axios

import axios from '../../utils/axios'; 

// ----------------------------------------------------------------------

const TABLE_HEAD = [
 { id: 'mtn_sme_bal', label: 'MTN SME BAL', alignRight: false },
{id: 'mtn_cg_bal', label: 'MTN CG BAL', alignRight: false},
{id: 'mtn_g_bal', label: 'MTN G BAL', alignRight: false},

{ id: 'airtel_sme_bal', label: 'AIRTEL SME BAL', alignRight: false },
{id: 'airtel_cg_bal', label: 'AIRTEL CG BAL', alignRight: false},
{id: 'airtel_g_bal', label: 'AIRTEL G BAL', alignRight: false},

{ id: 'glo_sme_bal', label: 'GLO SME BAL', alignRight: false },
{id: 'glo_cg_bal', label: 'GLO CG BAL', alignRight: false},
{id: 'glo_g_bal', label: 'GLO G BAL', alignRight: false},

{ id: 'mobile_sme_bal', label: 'MOBILE SME BAL', alignRight: false },
{id: 'mobile_cg_bal', label: 'MOBILE CG BAL', alignRight: false},
{id: 'mobile_g_bal', label: 'MOBILE G BAL', alignRight: false},


{ id: 'mtn_sme', label: 'MTN SME VENDING', alignRight: false },
{id: 'mtn_cg', label: 'MTN CG VENDING', alignRight: false},
{id: 'mtn_g', label: 'MTN G VENDING', alignRight: false},

{ id: 'airtel_sme', label: 'AIRTEL SME VENDING', alignRight: false },
{id: 'airtel_cg', label: 'AIRTEL CG VENDING', alignRight: false},
{id: 'airtel_g', label: 'AIRTEL G VENDING', alignRight: false},

{ id: 'glo_sme', label: 'GLO SME VENDING', alignRight: false },
{id: 'glo_cg', label: 'GLO CG VENDING', alignRight: false},
{id: 'glo_g', label: 'GLO G VENDING', alignRight: false},

{ id: 'mobile_sme', label: 'MOBILE SME VENDING', alignRight: false },
{id: 'mobile_cg', label: 'MOBILE CG VENDING', alignRight: false},
{id: 'mobile_g', label: 'MOBILE G VENDING', alignRight: false},

];

// ----------------------------------------------------------------------

export default function UserStockBalance() {
    const theme = useTheme();
  const { themeStretch } = useSettings();
  const [userList, setUserList] = useState([]);
 const [load, SetLoad] = useState(true);
  const [order, setOrder] = useState('asc');

  const [orderBy, setOrderBy] = useState('id');
  const [dense, Setdense] = useState(false);
  const AccessToken =  window.localStorage.getItem('accessToken');

  useEffect(()  => {
  axios.get(`/api/system/user/stockbalance/${AccessToken}/secure`).then(data => {
     setUserList(data.data?.stock_balance);
     SetLoad(false)
         }).catch(() => SetLoad(false));
 
  }, [AccessToken]);

  const handleRequestSort = (property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };




 

   

  





  function handleChangeDense(event){
   Setdense(event.target.checked);
  }

  return (
    <Page title="Stock Balance">
      <Container maxWidth={themeStretch ? false : 'lg'}>
        <HeaderBreadcrumbs
          heading="Stock Balance"
          links={[
            { name: 'Dashboard', href: PATH_DASHBOARD.root },
            { name: 'balance' },
          ]}
        />
        <Card>
      
          <Scrollbar>
            <TableContainer sx={{ minWidth: 800 }}>
              <Table
              size={dense ? 'small' : 'medium'}
              >
                <HabukhanListHead
                  order={order}
                  orderBy={orderBy}
                  headLabel={TABLE_HEAD}
                  onRequestSort={handleRequestSort}
                />

                <TableBody>
                  {userList.map((row) => {
                    const { username, mtn_sme_bal, mtn_cg_bal, mtn_g_bal, airtel_sme_bal, airtel_cg_bal, airtel_g_bal, glo_sme_bal, glo_cg_bal, glo_g_bal, mobile_sme_bal, mobile_cg_bal, mobile_g_bal,  mtn_sme, mtn_cg, mtn_g, airtel_sme, airtel_cg, airtel_g, glo_sme, glo_cg, glo_g, mobile_sme, mobile_cg, mobile_g  } = row;
                    return (
                      <TableRow
                        hover
                        key={username}
                        tabIndex={-1}
                      >
                        <TableCell align="left">{`₦${fCurrency(mtn_sme_bal)}`}</TableCell>
                        <TableCell align="left">{`₦${fCurrency(mtn_cg_bal)}`}</TableCell>
                        <TableCell align="left">{`₦${fCurrency(mtn_g_bal)}`}</TableCell>
                        <TableCell align="left">{`₦${fCurrency(airtel_sme_bal)}`}</TableCell>
                        <TableCell align="left">{`₦${fCurrency(airtel_cg_bal)}`}</TableCell>
                        <TableCell align="left">{`₦${fCurrency(airtel_g_bal)}`}</TableCell>

                        <TableCell align="left">{`₦${fCurrency(glo_sme_bal)}`}</TableCell>
                        <TableCell align="left">{`₦${fCurrency(glo_cg_bal)}`}</TableCell>
                        <TableCell align="left">{`₦${fCurrency(glo_g_bal)}`}</TableCell>

                        <TableCell align="left">{`₦${fCurrency(mobile_sme_bal)}`}</TableCell>
                        <TableCell align="left">{`₦${fCurrency(mobile_cg_bal)}`}</TableCell>
                        <TableCell align="left">{`₦${fCurrency(mobile_g_bal)}`}</TableCell>

                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(mtn_sme === 0 && 'warning') || (mtn_sme === 1 && 'success')}
                          >
                            {sentenceCase((mtn_sme === 0 && 'wallet') || (mtn_sme === 1 && 'active'))}
                          </Label>
                        </TableCell>
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(mtn_cg === 0 && 'warning') || (mtn_cg === 1 && 'success')}
                          >
                            {sentenceCase((mtn_cg === 0 && 'wallet') || (mtn_cg === 1 && 'active'))}
                          </Label>
                        </TableCell>
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(mtn_g === 0 && 'warning') || (mtn_g === 1 && 'success')}
                          >
                            {sentenceCase((mtn_g === 0 && 'wallet') || (mtn_g === 1 && 'active'))}
                          </Label>
                        </TableCell>
                        {/* airtel here */}
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(airtel_sme === 0 && 'warning') || (airtel_sme === 1 && 'success')}
                          >
                            {sentenceCase((airtel_sme === 0 && 'wallet') || (airtel_sme === 1 && 'active'))}
                          </Label>
                        </TableCell>
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(airtel_cg === 0 && 'warning') || (airtel_cg === 1 && 'success')}
                          >
                            {sentenceCase((airtel_cg === 0 && 'wallet') || (airtel_cg === 1 && 'active'))}
                          </Label>
                        </TableCell>
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(airtel_g === 0 && 'warning') || (airtel_g === 1 && 'success')}
                          >
                            {sentenceCase((airtel_g === 0 && 'wallet') || (airtel_g === 1 && 'active'))}
                          </Label>
                        </TableCell>
                        {/* glo here */}
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(glo_sme === 0 && 'warning') || (glo_sme === 1 && 'success')}
                          >
                            {sentenceCase((glo_sme === 0 && 'wallet') || (glo_sme === 1 && 'active'))}
                          </Label>
                        </TableCell>
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(glo_cg === 0 && 'warning') || (glo_cg === 1 && 'success')}
                          >
                            {sentenceCase((glo_cg === 0 && 'wallet') || (glo_cg === 1 && 'active'))}
                          </Label>
                        </TableCell>
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(glo_g === 0 && 'warning') || (glo_g === 1 && 'success')}
                          >
                            {sentenceCase((glo_g === 0 && 'wallet') || (glo_g === 1 && 'active'))}
                          </Label>
                        </TableCell>
                        {/* gmobile here */}
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(mobile_sme === 0 && 'warning') || (mobile_sme === 1 && 'success')}
                          >
                            {sentenceCase((mobile_sme === 0 && 'wallet') || (mobile_sme === 1 && 'active'))}
                          </Label>
                        </TableCell>
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(mobile_cg === 0 && 'warning') || (mobile_cg === 1 && 'success')}
                          >
                            {sentenceCase((mobile_cg === 0 && 'wallet') || (mobile_cg === 1 && 'active'))}
                          </Label>
                        </TableCell>
                        <TableCell align="left">
                          <Label
                            variant={theme.palette.mode === 'light' ? 'ghost' : 'filled'}
                            color={(mobile_g === 0 && 'warning') || (mobile_g === 1 && 'success')}
                          >
                            {sentenceCase((mobile_g === 0 && 'wallet') || (mobile_g === 1 && 'active'))}
                          </Label>
                        </TableCell>
                      </TableRow>
                    );
                  })}
                  
                </TableBody>

              </Table>
            </TableContainer>
          </Scrollbar>
          
          <FormControlLabel
        control={<Switch checked={dense} onChange={(event) => handleChangeDense(event)} />}
        label="Dense padding" align="left"
      />   
            
        </Card>
        <Backdrop
        sx={{ color: '#fff', zIndex: (theme) => theme.zIndex.drawer + 1 }}
        open={load}
      >
        <CircularProgress color="inherit" />
      </Backdrop>
      </Container>
    </Page>
  );





}

// ----------------------------------------------------------------------


 



  

  
